    --bs-navbar-disabled-color: rgba(0, 0, 0, 0.3);
    --bs-navbar-active-color: rgba(0, 0, 0, 0.9);
    --bs-navbar-brand-padding-y: 0.3125rem;
    --bs-navbar-brand-margin-end: 1rem;
    --bs-navbar-brand-font-size: 1.25rem;
    --bs-navbar-brand-color: rgba(0, 0, 0, 0.9);
    --bs-navbar-brand-hover-color: rgba(0, 0, 0, 0.9);
    --bs-navbar-nav-link-padding-x: 0.5rem;
    --bs-navbar-toggler-padding-y: 0.25rem;
    --bs-navbar-toggler-padding-x: 0.75rem;
    --bs-navbar-toggler-font-size: 1.25rem;
    --bs-navbar-toggler-icon-bg: url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' viewBox='0 0 30 30'%3e%3cpath stroke='rgba%280, 0, 0, 0.55%29' stroke-linecap='round' stroke-miterlimit='10' stroke-width='2' d='M4 7h22M4 15h22M4 23h22'/%3e%3c/svg%3e");
    --bs-navbar-toggler-border-color: rgba(0, 0, 0, 0.1);
    --bs-navbar-toggler-border-radius: 0.375rem;
    --bs-navbar-toggler-focus-width: 0.25rem;
    --bs-navbar-toggler-transition: box-shadow 0.15s ease-in-out;
    position: relative;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: space-between;
    padding: var(--bs-navbar-padding-y) var(--bs-navbar-padding-x);

  }

  .hamburger {
    display: block;
  }

  .navigation-menu ul {
    display: none;
  }

  .navigation-menu ul {
    // navigation menu is positioned to start 60px from the top of the document (which is directly below the navbar)
    position: absolute !important;
    top: 60px;
    left: 0;
    display: none;
    // stacks the li tags vertically 
    flex-direction: column;
    // makes menu span full height and width
    width: 100%;
    height: calc(100vh - 77px);
    background-color: rgba(0, 0, 0, 0.913);
    border-top: 1px solid black;
  }

  // Start my version of hover
  .navigation-menu.expanded a,
  .navigation-menu.expanded a:focus {
    display: flex;
    align-items: center;
    justify-content: space-between;
    padding: 10px 20px;
    font-family: var(--font-primary);
    font-size: 15px;
    font-weight: 500;
    color: rgba(255, 255, 255, 0.7);
    white-space: nowrap;
    text-transform: uppercase;
    transition: 0.3s;
  }

  .navigation-menu.expanded li {
    display: grid;
    font-size: 12px;
    line-height: 0;
    margin-left: 5px;
    text-align: center !important;
    align-items: center !important;
    // padding: 10px 20px;
  }

  .navigation-menu.expanded a li,
  .navigation-menu.expanded a:focus li {
    font-size: 12px;
    line-height: 0;
    margin-left: 5px;

  }

  .navigation-menu.expanded a:hover,
  .navigation-menu.expanded .active,
  .navigation-menu.expanded .active:focus,
  .navigation-menu.expanded li:hover>a {
    color: #fff;
  }

  // End my verison of Hover

  .navigation-menu ul {
    /* previous styles */
    display: none;
  }

  .navigation ul {
    position: absolute;
    inset: 0;
    padding: 50px 0 10px 0;
    margin: 0;
    background: rgba(0, 0, 0, 0.8);
    overflow-y: auto;
    transition: 0.3s;
    z-index: 9998;
    margin-left: 50px;


  }

  .navigation-menu.expanded {

    background-color: #232822;
  }

  .navigation-menu.expanded ul {
    display: flex;
    position: fixed;
    top: 0px;
    right: -100%;
    max-height: 100vh;
    width: 100%;
    max-width: 100vh;
    bottom: 0;
    transition: 0.3s;
    z-index: 1000;
    margin-top: 0px;
    margin-left: 0px;
    height: 100vh;



  }



}