*, ::after, ::before {
  box-sizing: border-box;
}

.sell {
  color: #1e9bff !important;
  font-weight: bold !important;
}

.hero {
  overflow-x: hidden;
  padding: 0;
}

.hero .carousel {
  width: 100%;
  min-height: 100vh;
  padding: 80px 0;
  margin: 0;
  position: relative;
}

.hero .carousel-item {
  position: absolute;
  inset: 0;
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
  overflow: hidden;
  z-index: 1;
  transition-duration: 0.4s;
  background-color: #52565e;
}

.hero .carousel-item::before {
  content: "";
  background-color: rgba(0, 0, 0, 0.7);
  position: absolute;
  inset: 0;
}

.hero .info {
  position: absolute;
  inset: 0;
  z-index: 2;
}

.pon {
  visibility: hidden !important;
  padding-bottom: 0 !important;
}
.gol {
  height: 0px;
}


@media (max-width: 768px) {
  .hero .carousel {
    min-height: 120vh;
  }

  .hero .info {
    padding-top: 50%;


  }

  .pon {
    display: flex;
    justify-content: center;
    padding-bottom: 35% !important;
    padding-top: 25% !important;
    visibility: visible !important;



  }

  .gol {
    height: 15% !important;
    position: absolute;

  }
}

.hero .info h2 {
  color: #fff;
  margin-bottom: 30px;
  padding-bottom: 30px;
  font-size: 56px;
  font-weight: 700;
  position: relative;
}

.hero .info h2:after {
  content: "";
  position: absolute;
  display: block;
  width: 80px;
  height: 4px;
  background: var(--color-primary);
  left: 0;
  right: 0;
  bottom: 0;
  margin: auto;
}

@media (max-width: 768px) {




  .hero .info h2 {
    font-size: 36px;
    /* color: coral; */
  }
}

.hero .info p {
  color: rgba(255, 255, 255, 0.8);
  font-size: 18px;
}

.hero .info .btn-get-started {
  font-family: var(--font-primary);
  font-weight: 500;
  font-size: 16px;
  letter-spacing: 1px;
  display: inline-block;
  padding: 12px 40px;
  border-radius: 50px;
  transition: 0.5s;
  margin: 10px;
  color: #fff;
  border: 2px solid var(--color-primary);
}

.hero .info .btn-get-started:hover {
  background: var(--color-primary);
}

.hero .carousel-control-prev {
  justify-content: start;
}

@media (min-width: 640px) {
  .hero .carousel-control-prev {
    padding-left: 15px;
  }
}

.hero .carousel-control-next {
  justify-content: end;
}

@media (min-width: 640px) {
  .hero .carousel-control-next {
    padding-right: 15px;
  }
}

.ic {
  background: none;
  font-size: 26px;
  line-height: 0;
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.6);
  border-radius: 50px;
  width: 54px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero .carousel-control-next-icon,
.hero .carousel-control-prev-icon {
  background: none;
  font-size: 26px;
  line-height: 0;
  background: rgba(255, 255, 255, 0.2);
  color: rgba(255, 255, 255, 0.6);
  border-radius: 50px;
  width: 54px;
  height: 54px;
  display: flex;
  align-items: center;
  justify-content: center;
}

.hero .carousel-control-prev,
.hero .carousel-control-next {
  z-index: 3;
  transition: 0.3s;
}

.hero .carousel-control-prev:focus,
.hero .carousel-control-next:focus {
  opacity: 0.5;
}

.hero .carousel-control-prev:hover,
.hero .carousel-control-next:hover {
  opacity: 0.9;
}

/* smaller screens */
@media (max-width: 376px) and (max-height: 668px) {
  .hero .info h2 {
    font-size: 26px;
    color: rgb(255, 80, 211);
    padding-top: 90px;
  }

  .hero .info h2 {
    color: #fff;
    margin-bottom: 10px;
    padding-bottom: 30px;

    font-weight: 700;
    position: relative;
  }

  .hero .info h2:after {
    content: "";
    position: absolute;
    display: block;
    width: 80px;
    height: 4px;
    background: var(--color-primary);
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }
}

/* bigger */
@media (min-width: 400px) and (max-height: 1000px) {
  .hero .info h2 {
    font-size: 36px;
    color: rgb(255, 80, 211);


  }

  .hero .info h2 {
    color: rgb(255, 255, 255);
    margin-bottom: 10px;


    font-weight: 700;
    position: relative;
  }

  .hero .info h2:after {
    content: "";
    position: absolute;
    display: block;
    width: 80px;
    height: 4px;
    background: var(--color-primary);
    left: 0;
    right: 0;
    bottom: 0;
    margin: auto;
  }

  .hero .info p {
    color: rgba(255, 255, 255, 0.8);
    font-size: 18px;
    padding-top: 25px;
  }


}