.bg-image {
  position: relative;
  overflow: hidden;
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center center
}

.mask {
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  width: 100%;
  height: 100%;
  overflow: hidden;
  background-attachment: fixed
}

.hover-overlay .mask {
  opacity: 0;
  transition: all .3s ease-in-out
}

.hover-overlay .mask:hover {
  opacity: 1
}

.hover-zoom img, .hover-zoom video {
  transition: all .3s linear
}

.hover-zoom:hover img, .hover-zoom:hover video {
  transform: scale(1.1)
}

.hover-shadow, .card.hover-shadow {
  box-shadow: none;
  transition: all .3s ease-in-out
}

.hover-shadow:hover, .card.hover-shadow:hover {
  box-shadow: 0 2px 15px -3px rgba(0, 0, 0, .16), 0 10px 20px -2px rgba(0, 0, 0, .1);
  transition: all .3s ease-in-out
}

.bg-fixed {
  background-attachment: fixed
}

.hover-shadow-soft, .card.hover-shadow-soft {
  box-shadow: none;
  transition: all .3s ease-in-out
}

.hover-shadow-soft:hover, .card.hover-shadow-soft:hover {
  box-shadow: 0 2px 25px -5px rgba(0, 0, 0, .07), 0 25px 21px -5px rgba(0, 0, 0, .04);
  transition: all .3s ease-in-out
}

.form-control {
  min-height: auto;
  padding-top: 4px;
  padding-bottom: 3.28px;
  transition: all .1s linear
}

.form-control:focus {
  box-shadow: none;
  transition: all .1s linear;
  border-color: #1266f1;
  box-shadow: inset 0px 0px 0px 1px #1266f1
}

.form-control.form-control-sm {
  font-size: .775rem;
  line-height: 1.5
}

.form-control.form-control-lg {
  line-height: 2.15;
  border-radius: .25rem
}

.form-outline {
  position: relative
}

.form-outline .form-helper {
  width: 100%;
  position: absolute;
  font-size: .875em;
  color: #757575
}

.form-outline .form-helper .form-counter {
  text-align: right
}

.form-outline .trailing {
  position: absolute;
  right: 10px;
  left: initial;
  top: 50%;
  transform: translateY(-50%);
  pointer-events: none
}

.form-outline .form-icon-trailing {
  padding-right: 2rem !important
}

.form-outline .form-control {
  min-height: auto;
  padding-top: .33em;
  padding-bottom: .33em;
  padding-left: .75em;
  padding-right: .75em;
  border: 0;
  background: transparent;
  transition: all .2s linear
}

.form-outline .form-control~.form-label {
  position: absolute;
  top: 0;
  max-width: 90%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  left: .75rem;
  padding-top: .37rem;
  pointer-events: none;
  transform-origin: 0 0;
  transition: all .2s ease-out;
  color: rgba(0, 0, 0, .6);
  margin-bottom: 0
}

.form-outline .form-control~.form-notch {
  display: flex;
  position: absolute;
  left: 0;
  top: 0;
  width: 100%;
  max-width: 100%;
  height: 100%;
  text-align: left;
  pointer-events: none
}

.form-outline .form-control~.form-notch div {
  pointer-events: none;
  border: 1px solid;
  border-color: #bdbdbd;
  box-sizing: border-box;
  background: transparent;
  transition: all .2s linear
}

.form-outline .form-control~.form-notch .form-notch-leading {
  left: 0;
  top: 0;
  height: 100%;
  width: .5rem;
  border-right: none;
