.nav-pills.menu-sidebar .nav-link {
  font-size: .8rem;
  background-color: transparent;
  color: #262626;
  line-height: 1.1rem;
  padding: 0 5px;
  font-weight: 400;
  transition: all .2s ease-in-out;
  text-transform: initial;
  margin-top: 3px;
  margin-bottom: 3px
}

.nav-pills.menu-sidebar .nav-link.active, .nav-pills.menu-sidebar .show>.nav-link {
